@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(251, 253, 254);
  color: #003755;
  font-family: danske_textregular;
  font-weight: 400;
  line-height: 1.5;
}

/* Footer Section  styling  */
.header-color,
.footerFoo {
  background-color: rgb(0, 55, 85) !important;
}

.landingpageGrid {
  position: center;
  top: 5%;
  left: 0;
  height: 100%;
  text-align: center;
}

.nalFotoFoo {
  width: 200px;
  padding-top: 1em;
}
.frontEndDiv {
  background-color: #d5d8dc;
  opacity: 0.8;
  width: 100%;
  margin: auto;
  border-radius: 1em;
}

.frontEndDiv h2 {
  font-size: 2em;
  font-weight: bold;
  color: #003755;
  text-align: center;
  padding-top: 0.2em;
}
.frontEndDiv hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.frontEndDiv p {
  color: black;
  font-size: 18px;
  padding: 1em;
  text-align: center;
}
.social-links {
  display: flex;
  justify-content: center;
  margin: auto;
}
.social-links i {
  color: #003755;
  font-size: 5em;
  padding: 5px;
}

.social-links a .icon {
  position: relative;
  transition: 0.5s;
  z-index: 3;
}

.social-links a:hover .icon {
  color: #239b56;
  transform: rotateY(360deg);
}

.d-block {
  width: 80rem;
  height: 20rem;
  margin-left: 1%;
  padding: 0.5%;
  padding-bottom: 1%;
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
}

.skillSetSec h4 {
  text-align: center;
}
.progressBarStyling {
  width: 80%;
  height: 1.5rem;
  margin-left: 10%;
}

.technicalSet {
  margin-left: 10%;
  font-size: 1.3rem;
}

.CardStyling {
  padding: 2rem;
}

.resumeGrid {
  width: 100%;
  padding: 0.5%;
}

.leftCol,
.rightCol {
  padding-left: 3em;
  padding-right: 1em;
}
.leftCol img {
  width: 10em;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* Recommendations sectin styling  */
.rightCol p {
  padding-top: 2%;
}

.recommendtCell {
  margin-top: 2%;
}

.recommendtCell img {
  margin-top: 1%;
}

/* Summary tab styling  */
.summaryTab {
  background: #fff;
  display: inline-block;
  position: relative;
  width: 90%;
  border-style: inset;
}

.summaryTab p {
  padding: 4%;
  font-size: larger;
}

.summaryTab p:hover {
  box-shadow: 0 14px 28px #5499c7, 0 10px 10px #5499c7;
}

/* Experience | education | recommendation p styling  */

.Experience {
  width: 100%;
  border-radius: 10px;
  background: rgba(215, 233, 241);
  display: table;
  cursor: pointer;
  transition: 1s;
  padding: 2%;
  margin-bottom: 1%;
  margin-top: 1%;
}
.Experience:hover {
  background: linear-gradient(315deg, #5499c7, #5499c7);
  box-shadow: inset -30px -10px 10px #5499c7, inset 30px 10px 10px #5499c7;
}

/* Skill section styling  */
.progressBarStylingx {
  width: 90% !important;
  height: 1.5rem;
}

